*{
  margin: 0;
}

body {
  background-color: #f5f6fa;
}

.App{
  display: flex;
  justify-content: space-evenly;
  padding: 20px
}

.app_header > h1 {
  color: #fc3c3c;
  font-size: 2rem;
}

.app_dropdawn {
  background-color: white;
}

.app_header{
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  
}

.app_left {
  flex: 0.9;
}

.app_stats{
  display: flex;
  justify-content: space-between;
}

.app_right{
  display: flex;
  flex-direction: column;
}

.app-right .MuiCardContent-root {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.app_graph {
  flex-grow: 1;
}

.app_graphTitle {
  margin-top: 20px;
  margin-bottom: 20px;
}

@media (max-width: 999px){
.App{
  flex-direction: column;;
  }
}